import React,{Component} from 'react';
import {message,Input,Modal,Button,Form,Select} from 'antd';
import '../index.less';
import {util,emitter,minAjax} from '../../../../common/common';
import {prefix,productPrefix,isProductUrl} from '../../../../interfaces/interfaces';
import {allMetaData} from '../../../../components/select';

export default class TestModal extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('goto-login', ({config,body})=>{
      let account = window.sessionStorage.getItem('account');
      let pwd = window.sessionStorage.getItem('pwd');
  		this.setState({visible:true,registerTag:undefined,formValues:{account,pwd},url:config.url,config});

      // if(body){
      //   let url=JSON.parse(body).url;
      //   let urlPrefix = url.substring(0,url.indexOf('reffer='));
      //   let targetUrl;
      //   if(isProductUrl(config.url)){
      //     targetUrl=productPrefix+'fbi/api/v1/ssoagentlogin/jumplogin?reffer='+encodeURIComponent('http:'+productPrefix.substring(0,productPrefix.length-1)+'?target='+encodeURIComponent(window.location.href));
      //   }else{
      //     targetUrl=urlPrefix+'reffer='+encodeURIComponent(prefix+'fbi/api/v1/index?target='+encodeURIComponent(window.location.href));
      //   }
      //   window.location.href=targetUrl;
      // }
  	});
  	this.state={

  	};
  }

  handleLogin=()=>{
    let {registerTag} = this.state;
    let validFields;
    if(registerTag){
      validFields = ['name','phone','pass','pwd1','groupIds'];
    }else{
      validFields = ['phone','pass'];
    }
    util.validateFields.call(this,values=>{
      let {phone,name,pass,groupIds} = values;
      if(registerTag){
        // util.ajaxJson(this.getPrefix()+'flow/user/register',{account,name,pass,groupIds:JSON.stringify(groupIds)},res=>{
        //   if(res.success && !res.errMsg){
        //     message.success('!');
        //     window.sessionStorage.setItem('name',name);
        //     window.sessionStorage.setItem('groupIds',JSON.stringify(groupIds));
        //     window.location.reload();
        //   }else{
        //     message.error(res.errMsg);
        //   }
        // });
      }else{
        util.ajaxJson(this.getPrefix()+'flow/biz/login',{phone,pass,noErrAlert:true},result=>{
          if(result.success && !result.errMsg){
            let res=result.data[0];
            window.sessionStorage.setItem('name',res.name);
            window.sessionStorage.setItem('userId',res.id);
            window.sessionStorage.setItem('phone',res.phone);
            emitter.emit('init');
            if(!window.noNeedRefreshCurrentMenu){
              emitter.emit('fc-settlement-refreshcurrentmenu');
            }
            minAjax(this.state.config);
            this.setState({visible:false});
          }else{
            message.error(result.errMsg);
          }
        });
      }
      
    },validFields);
  }

  getPrefix=()=>{
    let {url} = this.state;
    if(!url || !productPrefix){
      return prefix;
    }
    if(url.startsWith(productPrefix)){
      return productPrefix;
    }
    return prefix;
  }

  toRegister=()=>{
    this.setState({registerTag:true});
  }

  handleCancel=()=>{
    this.setState({visible:false});
  }

  render() {
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    let {formValues,registerTag} = this.state;
  	return (<Modal wrapClassName="login-modal" title={registerTag?'':''} visible={this.state.visible} width={400} onCancel={this.handleCancel} maskClosable={false}
          footer={[
            <Button key="login" type="primary" onClick={this.handleLogin}>{registerTag?'':''}</Button>,
            <Button key="close" onClick={this.handleCancel}></Button>
          ]}>
          <Form id="log-container-form" className="login-container">
            {registerTag&&<Form.Item {...formItemLayout} label="">
              <Input {...util.getField.bind(this)('name',{rules:[{validator:v=>{
                return !!v;
              },message:''}]})}/>
            </Form.Item>}
            <Form.Item {...formItemLayout} label="()">
              <Input {...util.getField.bind(this)('phone',{rules:[{validator:v=>{
                return !!v;
              },message:''},{validator:v=>{
                return /^1[0-9]{10}$/.test(v);
              },message:'111'}]})}/>
            </Form.Item>
            <Form.Item {...formItemLayout} label="">
              <Input type="password" {...util.getField.bind(this)('pass',{rules:[{validator:v=>{
                return !!v;
              },message:''}]})} onPressEnter={this.handleLogin}/>
            </Form.Item>
            {registerTag&&<div>
              <Form.Item {...formItemLayout} label="">
                <Input {...util.getField.bind(this)('pwd1',{rules:[{validator:v=>{
                  return !!v;
                },message:''},{validator:v=>{
                  let {formValues} = this.state;
                  return formValues.pwd === v;
                },message:''}]})}/>
              </Form.Item>
            </div>}
            <div className="register-area">
              {false&&<a onClick={this.toRegister}></a>}
              {registerTag&&<a onClick={()=>{
                this.setState({registerTag:undefined});
              }}></a>}
            </div>
          </Form>
      </Modal>);
  }
}