export let domain = 'localhost:8184';
export let productDomain = '47.118.49.154';
if(window.location.href.indexOf('47.118.49.154')>-1){
	domain=productDomain;
}

window.isProd=function(){
	if(window.location.hash=='#pre'||window.location.hash=='#presync'){
	    return false;
	}
	return window.envTag && window.envTag.indexOf('product') > -1 || isProductUrl(domain);
};
window.isDebug=function(){
	return window.location.hash&&window.location.hash.indexOf('.debug') > -1;
}
window.cannotDo=function(){
	return window.isProd();
};

let basePathSuffix='flow/biz/';

export let prefix = '//' + domain + '/';
export let productPrefix = '//' + productDomain + '/';

export function isProductUrl(url){
	return url&&!url.startsWith('pre')&&url.indexOf('alibaba-inc.com') > -1;
}
const interfacesData = {
    
};

let interfaces = interfacesData;

export default interfaces;
export let urlPrefix = prefix + basePathSuffix;
export let urlProductPrefix = productPrefix+basePathSuffix;