import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,permissions,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import AddMemberModal from './addMemberModal';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    this.config = {
      title:'',
      key:'Member',
      dialog:{width:1060},
      delayQueryTable:1000,
      queryCond:{
        conditions:[[{key:'phone'},{key:'name'},{key:'operator'}]],
        extButtons:[<Button key="1" type="primary" size="small" onClick={()=>emitter.emit('addMemberModal')}></Button>],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'10%',
          rules:[{required: true, message: ''}],
          label:''
        },{
          key:'phone',
          width:'10%',
          rules:[{required: true, message: ''},{validator:v=>{
            return /^1[0-9]{10}$/.test(v);
          },message:'111'}],
          label:''
        },{
          key:'amount',
          width:'10%',
          rules:[{required: true, message: ''}],
          label:''
        },{
          key:'billType',
          width:'10%',
          rules:[{required: true, message: ''}],
          enums:[{key:'',text:''},{key:'',text:''},{key:'',text:''},{key:'',text:''}],
          label:''
        },{
          key:'remainTimes',
          width:'10%',
          label:''
        },{
          key:'operator',
          width:'10%',
          label:''
        },{
          key:'applyTime',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:''
        },{
          key:'expireTime',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:''
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:''
        }
      ],
      // operationAddon:record=>{
      //   return (<span><a onClick={()=>{
          
      //   }}></a></span>);
      // },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:formData=>{
          formData.ext=JSON.stringify({staffs:formData.staffs});
          delete formData.staffs;
          return formData;
        }
      },
      table:{
        bordered:true,
        size:'small',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'MemberOperation?opt=queryMember'},
        // updateInterface:{func:urlPrefix+'MemberOperation?opt=updateMember'},
        deleteInterface:{func:urlPrefix+'MemberOperation?opt=deleteMember',params:{id:'id'}}
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <AddMemberModal />
      </div>
    );
  }
}