import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,permissions,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('addMemberModal', ()=>{
        let formValues={roomId:rooms.length>0?rooms[0].id+'':undefined,applyTime:moment()};
        this.setState({visible:true,formValues});
      
    });
  	this.state={
      formValues:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      let {formValues} = this.state;
      util.ajaxJson(urlPrefix+'MemberOperation?opt=insertMember', {...formValues}, res=>{
        if(res.success){
          message.success(`!`);
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryMemberTable`, undefined, true);
    this.setState({visible:false});
  }

  render() {
    let {visible,formValues} = this.state;
  	return (<Modal title={``} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            
         </Button>]
      }>
          <Form className="add-member-modal">
            <Form.Item className="" label="" required>
                <Input {...util.getField.call(this,'name',{rules:[{required:true,message:''}]})} />
            </Form.Item>
            <Form.Item className="" label="">
                <Input {...util.getField.call(this,'phone',{rules:[{validator:v=>{
                if(!v)return true;
                return /^1[0-9]{10}$/.test(v);
              },message:'111'}]})} />
            </Form.Item>
            <Form.Item className="" label="()" required>
                <InputNumber {...util.getField.call(this,'amount',{rules:[{required:true,message:''}]})} />
            </Form.Item>
            <Form.Item className="" label="" required>
              <Select {...util.getField.bind(this)('billType',{rules:[{required:true,message:''}]})}>
                {[{key:'',text:''},{key:'',text:''},{key:'',text:''},{key:'',text:''}].map(it=>{
                  return <Select.Option key={it.key} value={it.key}>{it.text}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            {formValues.billType==''&&<Form.Item className="" label="" required>
                <InputNumber {...util.getField.call(this,'remainTimes')} />
            </Form.Item>}
            <Form.Item className="" label="" required>
                <DatePicker {...util.getField.call(this,'applyTime')} />
            </Form.Item>
            <Form.Item className="" label="" required>
              <Select {...util.getField.bind(this)('roomId',{})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;