import React,{Component} from 'react';
import {message,Input,Modal,Button} from 'antd';
import {util,emitter} from '../../../../common/common';
import './index.less';
export default class JsonEditor extends Component {
  constructor(...args) {
  	super(...args);
  	this.state={
  	};
  }

  UNSAFE_componentWillReceiveProps(nextProps){
    if(nextProps&&nextProps.value !== this.props.value){
      if(this.editor){
        if(this.editor.getValue()!==nextProps.value){
          // onChange
          this.noOnChange=true;
          this.editor.setValue(nextProps.value===undefined?'':nextProps.value);
          this.noOnChange=undefined;
        }
      }
    }
    if(nextProps&&nextProps.height !== this.props.height){
      if(this.editor){
        let wrapper = this.editor.getWrapperElement();
        if(wrapper){
          let height = nextProps.height;
          wrapper.style.height=height+'px';
        }
      }
    }
  }

  componentDidMount(){
    if(this.editor){
      return;
    }
    window.setTimeout(()=>{
      if(!this.el){
        return this.componentDidMount();
      }
      this.editor = window.CodeMirror.fromTextArea(this.el, {
        lineWrapping: true,
        foldGutter: true,
        gutters:this.props.gutters?this.props.gutters:["CodeMirror-linenumbers", "CodeMirror-foldgutter","CodeMirror-lint-markers"],
        lint: this.props.lint==="false"?false:true,
        matchBrackets: this.props.matchBrackets===undefined?true:this.props.matchBrackets,
        autoCloseBrackets:true,
        indentUnit:2,
        mode: "application/json",
        theme:this.props.theme?this.props.theme:'default',
        styleActiveLine: this.props.styleActiveLine===false?false:true
      });
      this.editor.setOption("extraKeys", {  
        Tab: function(cm) {
          if(cm.getSelection()){
            return window.CodeMirror.Pass;
          }else{
            let spaces = Array(cm.getOption("indentUnit") + 1).join(" ");
            cm.replaceSelection(spaces);
          }
        },
        F8:cm=>{
          if(this.props.onKeyPress){
            this.props.onKeyPress('F8');
          }
        }
      });
      if(this.props.onBlur){
        this.editor.on('blur', e=> {
          this.props.onBlur(e);
        });
      }
      this.editor.on('change', ()=> {
        if(!this.noOnChange)
          this.props.onChange(this.editor.getValue());
      });
      this.noOnChange=true;
      if(this.props.value)this.editor.setValue(this.props.value);
      this.noOnChange=undefined;
    },400);
  }

  componentWillUnmount(){
    if(this.editor){
      setTimeout(()=>{
        this.editor.toTextArea();
        this.editor = undefined;
      },100);
      
    }
  }

  render() {
  	return (<div className="json-editor"><textarea style={{display:'none'}} placeholder={this.props.placeholder} className={this.props.className} value={this.props.value} onChange={e=>{
            }} ref={el=>{
              this.el=el;
            }}></textarea></div>);
  }
}